        <img src="img/plus.svg" class="faq__item-icon" />
        <p class="faq__item-name">What is an escrow service?</p>
      </div>
      <div class="faq__item-info">
        <p>
          An escrow service is a third-party intermediary that helps facilitate
          secure transactions between two parties who don't know each other. It
          provides a safe and secure way for buyers and sellers to conduct
          business online without having to trust each other. <br />
          <br />
          ‍ The way an escrow service works is that the buyer sends the payment
          to the escrow service instead of directly to the seller. The escrow
          service holds the payment until the buyer receives the goods or
          services and confirms that they are satisfied with the transaction.
          Once the buyer confirms their satisfaction, the escrow service
          releases the payment to the seller. <br />
          <br />
          ‍ The use of an escrow service can help protect both the buyer and the
          seller from potential fraud and scams. It provides an added layer of
          security and ensures that both parties fulfill their obligations as
          agreed upon. Escrow services can be used for various types of
          transactions, such as online purchases, real estate transactions, and
          business acquisitions.
        </p>
