import Vue from 'vue';
import VueRouter from 'vue-router';
// import App from "@/App"
import Home from "@/pages/Home.vue"

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "Home",
		component: Home
	},
	{
		path: "*",
		component: Home
	}
];

const router = new VueRouter({
	mode: 'history',
	// base: '/test2/',
	routes,
});

export default router;