        "ANDREW":
          "We have had 4 deals with Marx in the last month - all successful! Everything was adequate, honest and very professional. We will continue to work only with him in the future. Thank you!",
        ANGRY:
          "I want to share a review about escrow <span>@MarxSafe.</span> Great guy! I am glad there are such adequate people who helped to complete the deal at the right level, without any faults! My sincere recommendation!",
        "JAMES":
